us va fumao y est√° gritando por un juego, audio pa ti pa que veas lo que tengo que aguantar. Pasa una avioneta medio cerca del suelo, na eso lo tiene que ver mi ni√±a. Si alguna vez crees que est√°s siendo pesada, ignora ese pensamiento y h√°blame pq me dar√°s una alegr√≠a. Eso s√≠, me preocupa de verdad que duermas bien y que descuides tus estudios por quedarte hasta las 385782 de la noche jugando o hablando o lo que sea. Me gusta ser una de tus prioridades pero como mucho tengo que ser la segunda, la primera tienes que ser t√∫.")]),_c('p',[_vm._v("De verdad que te amo much√≠simo. Eres la √∫nica persona que est√° en mi mente 24/7 (sin exagerar, da igual lo que haga que no hay manera de que salgas de mi cabeza), la √∫nica por la que romper√≠a mi horario de sue√±o (otra vez), la √∫nica que me importa si le va bien en la vida, la √∫nica que me preocupa si ha comido y cenado, la √∫nica que me preocupa si se ha peleado con alguien, la √∫nica que tengo miedo de perder y la √∫nica que quiero que se quede a mi lado el resto de mi vida.")]),_c('p',[_vm._v("Podr√≠a hacer un libro de 85756352685 p√°ginas intentando explicar lo que siento por ti y no ser√≠a suficiente. El d√≠a que puedas leer mi mente, entender√°s cu√°nto te quiero, ver√°s que todo lo que te digo, lo digo de coraz√≥n.")]),_c('p',[_vm._v("Eres perfecta. Amo absolutamente todo de ti. Tu voz, tu risa, tu cara, tu cuerpo, tu personalidad... No hay nada que me haga m√°s feliz que pasar rato contigo. No s√© si soy el primero que te ha hecho sentir como dices que te sientes pero te puedo asegurar que t√∫ s√≠ lo eres. He descubierto todo un mundo de emociones que no sab√≠a que se pod√≠an sentir por otra persona gracias a ti. Los dolores de coraz√≥n, la \"inquietud feliz\" (no s√© como explicarlo) esperando un mensaje tuyo, la sonrisilla tonta que me sale cuando te hago re√≠r... A veces tengo miedo de ser pesado con tanto te quiero y te amo pero no puedo evitarlo. Me has convertido en el hombre m√°s feliz del planeta y no quiero que lo dudes ni un solo d√≠a de tu vida.")]),_c('p',[_vm._v("Ya me hac√≠as muy feliz mucho antes de ser consciente de ello. Cuando te perd√≠ por un tiempo me di cuenta que eras mucho m√°s importante para m√≠ de lo que cre√≠a. Lo pas√© muy mal, como si hubiera muerto un ser querido. Me encantaba hablar contigo y daba por hecho que durar√≠a para siempre, y cuando vi que todo se puede acabar de repente, se me cay√≥ el mundo al suelo. Eras mi persona favorita mucho antes de todo esto. Pr√°cticamente desde que te conoc√≠. Lo mejor de mis d√≠as era hablar contigo. Ten√≠as un humor muy parecido al m√≠o, una personalidad graciosa y alegre, me vacilabas igual que yo a ti... Eras un foco de luz en mi vida. No pod√≠a creer que se hab√≠a acabado la √©poca de hablar 24/7 por hangouts, que hasta ah√≠ hab√≠a llegado todo. Pensaba que lo superar√≠a pero de verdad que no hubo d√≠a donde no pensara en esos tiempos, y en la vez que te dije que yo no me ir√≠a. Me sent√≠a culpable porque pensaba que se apag√≥ la chispa cuando me pele√© con juan y poco a poco dejamos de hablar. Tambi√©n lo estaba pasando mal por otras cosas y ya ni me levantaba de la cama, lo que hizo que no tuviera cosas que contarte y no sab√≠a como hablar contigo sin aburrirte. Lo pasaba muy mal viendo como cada vez se hac√≠an m√°s cortas nuestras conversaciones y ver que te estaba perdiendo poco a poco me estaba destrozando aun m√°s. Siempre lo has sido y no supe mantenerte a mi lado. No hay cosa de la que me arrepienta m√°s que de dejarte de hablar. Cada d√≠a me iba a una fecha aleatoria en el Hangouts y me pasaba un rato largo leyendo tu chat. A lo mejor por eso me acuerdo tan bien de detalles que parecen insignificantes. Habl√°bamos de absolutamente todo. A veces simplemente enviabas 385828582 stickers del Hangouts, otras ten√≠amos conversaciones medio serias, otras nos est√°bamos media hora poniendo letras aleatorias. La de veces que he pensado \"habl√°bamos tanto y ahora no s√© que decirle\". De verdad que nunca te super√©.")]),_c('p',[_vm._v("La bendita solicitud de seguimiento. El mejor regalo de cumplea√±os de mi vida. La vi cuando estaba en un bar con 3 mataos y se me puso tremenda cara. Uno de esos mataos me pregunt√≥ si hab√≠a ganado la loter√≠a, y era mucho mejor que eso. Acepto la solicitud, entro en el chat, y me quedo en blanco. Segu√≠a sin saber que decirte. Despu√©s de todo ese tiempo en silencio, segu√≠a igual de retigga que antes. Decid√≠ dejar que me hablaras tu primera y pues eso. Esque no te puedes imaginar lo contento que me puse. Me iba el coraz√≥n como si me hubiera metido 90 monsters de mango. Macuerdo que vi la foto que ten√≠as en la moto de agua y pens√©, \"esta pava est√° viviendo una vida de millonaria y si hablamos, se volver√° a aburrir de m√≠\" xd. Te agradezco hayas tenido el valor que me falt√≥ a m√≠ para volver a hablarme. Fui un idiota y un cobarde al desperdiciar una amistad como la que ten√≠a contigo. Te juro que no volver√° a pasar. No quiero volver a perderte por mi culpa. No quiero volver a vivir todo eso. As√≠ que perdona si a veces soy pesado con que si te vas a ir o vas a dejar de quererme y todo eso. Tengo miedo de verdad üòî. Me cuesta asimilar que algo tan bueno como t√∫ me est√© ocurriendo a m√≠. Estoy muy agradecido de todo el tiempo que estuvimos hablando. Era (y soy) muy feliz gracias a ti.")]),_c('p',[_vm._v("Voy a parar ya me estoy enrollando demasiado y no te wa hacer leer una novela de mis sentimientos. Que pena tener que esperar a tu cumple pa enviarte esto. Hace 2 semanas que superamos el r√©cord de las ara√±as con Fius y Pol y estaba tan motivado decid√≠ cambiar tu acertijo de cumplea√±os por una carta explicando lo que haces por m√≠ sin darte cuenta. La vdd pensaba que se me dar√≠a peor. Soy horrible pa estas cosas y pensaba que escribir√≠a 2 p√°rrafos y me quedar√≠a en blanco pero llevo tremenda parrafada que me da pereza hasta a m√≠ leerlo.")]),_c('p',[_vm._v("Creo que nunca te he dicho que sigo sintiendo lo mismo con la misma intensidad que el d√≠a que nos confesamos. Sigo con el coraz√≥n acelerado y muy feliz pr√°cticamente todo el d√≠a todos los d√≠as. Todo esto gracias a ti. Es raro decir esto pero gracias a dios que dijiste que mateo sin camiseta te pone. Sino no a lo mejor no me habr√≠a dado cuenta que te amo, o no me habr√≠a abierto a ti de esa manera, y como consecuencia a lo mejor tu a m√≠ tampoco, y nunca habr√≠a pasado nada de esto.")]),_c('p',[_vm._v("S√© que probablemente es un desastre de carta. Habr√© repetido lo mismo 7 veces en 4 p√°rrafos distintos pq lo he escrito todo sobre la marcha y en varios d√≠as distintos y hay cosas que no s√© si ya las hab√≠a dicho antes o no. Hoy me has mencionado que tmbn me est√°s haciendo una carta. Aun queda un cristo pa mi cumple. No s√© si podr√© esperar tanto pero weno, voy a hacer lo posible por mantener mi curiosidad a raya y no estropear la sorpresa. A diferencia de ti, yo no te dir√© que estoy haciendo una carta. Bueno, lo estoy haciendo en el bloc de notas del movil. No s√© si se puede considerar carta. Bueno, hasta aqu√≠ he llegado de momento. Si ma√±ana se me ocurren m√°s cosas las ir√© poniendo.")]),_c('p',[_vm._v("Pos na, se ma escapado lo de que te estoy haciendo una carta. No me ha dado tiempo a borrar el mensaje de Instagram üòî. He rele√≠do toda mi carta para ver si me he dejado algo, y parece casi un diario. Poco m√°s y la separo por fechas o por cap√≠tulos. Pensandolo bien, tampoco es mala idea. Cap√≠tulo 1: lo que siento por ti. Cap√≠tulo 2: la √©poca oscura. Cap√≠tulo 3: el reencuentro. No s√© si es mejor ordenar bien la carta o dejarla tal cual la estoy pensando para que se vea lo desordenada que es mi mente cuando pienso en ti.")]),_c('p',[_vm._v("Tremenda parrafada te solt√© ayer. El alcohol me ha dado el valor suficiente para preguntarte si soy la primera persona por la que sientes todo lo que me dices que sientes y me has dicho que si. A lo mejor es ego√≠sta pero me hace feliz saber eso. Me hace muy feliz saber que alguien como t√∫ me quiere como nunca quiso a nadie. De todas las personas del planeta, me ama la √∫nica persona que quiero que me ame.")]),_c('p',[_vm._v("Gracias por dedicarme tiempo aunque est√©s triste o estresada o te encuentres mal. A veces tengo malos d√≠as o por cualquier motivo no estoy muy hablador y noto como que te respondo muy seco o simplemente con un \"tremendo\" o algo as√≠ porque no s√© qu√© decirte y me siento bastante mal por eso. Que me est√©s escribiendo una parrafada y yo te conteste con s√≥lo una palabra me sabe mal. Y tambi√©n me pasa al rev√©s, si estoy teniendo un buen d√≠a y me apetece hablar, a lo mejor me pongo a hablar de otro tema, y no le he dado importancia a algo que a lo mejor para ti s√≠ que es importante. De verdad que no lo hago con mala intenci√≥n. Aunque no est√© hablador, me encanta cuando me escribes y me interesa todo lo que tengas que decirme. Si cambio de tema no es pq no me importa lo que dices. De vdd que no ha habido ni un solo d√≠a, hora, minuto o segundo que me haya dado pereza hablarte. Si a veces parece que no te estoy prestando atenci√≥n, no es verdad. Me encanta hablar contigo y puedes estar segura que nunca me has aburrido. Significas much√≠simo para m√≠ y veces tengo la sensaci√≥n de que no soy suficiente para ti. Hablar contigo hace mil veces mejor mi d√≠a y cualquier cosa mala que me pase.")]),_c('p',[_vm._v("Me entiendes cuando me preocupa algo. No me culpas a m√≠ por sentirme de cierta manera por x cosas, sino que me escuchas y me entiendes. No digo que siempre tenga raz√≥n y que tienes que aceptar todo lo que digo, yo tengo much√≠simos errores. Me refiero a que no tengo que estar siempre a la defensiva contigo. Hasta \"discutir\" (por decirlo de alguna manera) es satisfactorio contigo pq podemos hablar bien.")]),_c('p',[_vm._v("Hoy nos hemos medio peleado. Por algo que yo creo que es injusto. Toy en discord callado pq no quieres hablarme. Ahora mismo quiero preguntarte si quieres que me vaya pero me da miedo que me digas que s√≠. A veces de verdad me gustar√≠a que no te diera miedo o pereza o verg√ºenza o lo que sea hablar las cosas, pero s√© que es dif√≠cil y que no debo exigirte demasiado de primeras. A mi tambi√©n me pasa a veces que me quedo callado cuando algo me sabe mal, en vez de decirlo al momento. Cuando se calme un poco la cosa te hablar√© por wasap o algo. Aun que est√© ahora algo mosqueado te sigo queriendo much√≠simo. Te quiero cuando est√°s contenta, triste o enfadada. Te sigo amando de verdad. S√© que contigo se puede hablar y que acabaremos arregl√°ndolo. Es una de las razones por las que te quiero tant√≠simo. A lo mejor para cuando te env√≠e esto ya ni te acuerdas de qu√© ha pasado hoy. Puede que no me creas despu√©s de todo esto pero te juro que no te voy a superar. Nunca. Es literalmente imposible que todo el estofado de emociones que me haces sentir, desaparezcan. Ni en 2857284828 a√±os voy a dejar de sentir algo por ti. Si te pierdo por cualquier motivo, te recordar√© como lo m√°s valioso que he perdido en mi vida. Aunque haga mi vida, siga adelante con el grupo, me mude a otro sitio, o lo que sea, te echar√© de menos si no est√°s conmigo. Has ocupado un sitio en mi coraz√≥n del que nadie ni nada podr√° desahuciarte nunca. Eres una parte tan grande de mi vida que ya no sabr√≠a c√≥mo vivirla sin ti. Sigo sin entender c√≥mo has generado tanto impacto en m√≠, estando a 700km de distancia. De verdad que amo c√≥mo eres, y c√≥mo me tratas.")]),_c('p',[_vm._v("Al final hemos hablado antes de lo que pensaba y ya se ha arreglado. Espero que no te quede ninguna astilla o algo que creas que no hemos hablado lo suficiente. Si alguna vez peleamos y lo hablamos, y sigues sin estar satisfecha de c√≥mo ha quedado la cosa, puedes volver a hablarme sobre lo que quieras. Aunque hayan pasado meses, vuelve a sacar el tema. Creo que me quieres much√≠simo y me ves como una buena persona. No quiero que eso cambie. No quiero que por una discusi√≥n, cambie la forma en la que me ves. Quiero solucionarlo todo al 100%. No quiero que te quede ninguna inseguridad o que sientas que algo ha sido injusto. De vdd que sigue habiendo momentos en los que recuerdo cuando te dije \"me he ido a jugar con otra üíÖ\" y cuando me fui del discord sin decir nada, y a√∫n me siento horrible por eso. Da igual cu√°nto me disculpe que no puedo evitar pensar en que te trat√© fatal. He hecho muchas cosas mal en la vida y espero haber aprendido lo suficiente como para no repetirlas contigo. Quiero darte mi mejor versi√≥n de verdad.")]),_c('p',[_vm._v("Hace 4827582 p√°rrafos que he dicho que paraba de escribir ya pq me estaba enrollando y aqu√≠ sigo. No puedo parar üòî.")]),_c('p',[_vm._v("Desde hace much√≠simos a√±os que mi objetivo ha sido alejarme de todos e irme s√≥lo a una casa en la monta√±a con un husky. Pero cuando digo s√≥lo, me refiero a s√≥lo sol√≠simo. No quer√≠a ver a nadie nunca m√°s. Me daba igual todo. Estaba convencid√≠simo de ello. No s√© c√≥mo lo has hecho, pero ahora no me imagino una vida sin ti. Has hecho que me vuelvan a importar las cosas. Has hecho que ya no quiera estar solo. Quiero tu compa√±√≠a. Quiero despertar cada d√≠a y que est√©s ah√≠ conmigo. A lo mejor no en la monta√±a, porque al final quieres ser poli, y yo soy programador, y en la monta√±a no s√© si necesitan mucho de eso. No he pensado demasiado en c√≥mo ser√° nuestro futuro. Simplemente s√© que da igual d√≥nde acabemos, mi hogar eres t√∫. Mientras sea a tu lado, s√© que ser√© feliz.")]),_c('p',[_vm._v("Hoy me has dicho que sigues pensando mal de ti por cosas del pasado. Yo te aseguro que no eres mala persona. Me parece injusto que pienses eso de ti. Todo lo que me has dicho que crees que es tu culpa, no lo es. Son cosas que se podr√≠an haber evitado f√°cilmente por la otra parte. De verdad que no debes machacarte por eso. Porfa intenta analizarlo l√≥gicamente. No pienses en lo que t√∫ podr√≠as haber hecho para evitarlo, sino en lo que la otra parte podr√≠a haber hecho para ayudarte a corregir tus errores si se pueden llamar errores siquiera. No me has dicho qu√© hiciste mal, simplemente que no te dabas cuenta. Estoy seguro de que no fue para tanto, simplemente la acumulaci√≥n de malos sentimientos a lo largo del tiempo lo transformaron en algo malo. Piensa en lo que paso de forma l√≥gica y te dar√°s cuenta que ten√≠as mucho menos control sobre eso de lo que crees. No es tu culpa que alguien se quede en un sitio donde no est√° feliz. No es tu culpa que alguien no te diga lo que cree que est√°s haciendo mal. No es tu culpa no poder leerle la mente. Quiero que lo entiendas. Entiende que te enga√±as a ti misma pensando que podr√≠as haberlo evitado. Y eso no te hace bien ni a ti, ni a m√≠. Me duele que pienses eso de ti. S√© que alg√∫n d√≠a te dar√°s cuenta de lo buena persona que eres en realidad.")]),_c('p',[_vm._v("Me has mencionado ya bastantes veces que en tu carta est√°s escribiendo sobre la √©poca antes de dejarnos de hablar. Voy a intentar explicar c√≥mo te ve√≠a yo, aunque ya haya hablado antes un poco sobre esto. Eres lo mejor que me hab√≠a pasado en much√≠simo tiempo, y me atrever√≠a a decir que en toda mi vida. Al igual que ahora, hace a√±os, te escrib√≠a dando igual en qu√© situaci√≥n estaba. Si estaba con amigos o en un bar o en clase o lo que sea, buscaba cualquier momento pa escribirte. Te escrib√≠a en el coche yendo al instituto, te escrib√≠a en clase cuando el profe no miraba, te escrib√≠a en el recreo, te escribia en los 5 minutos que ten√≠amos entre clases... Me interesaba de verdad lo que hac√≠as. Me dol√≠a cuando estabas triste y me alegrabas el d√≠a si ve√≠a que estabas contenta. Tambi√©n mostrabas inter√©s por lo que hac√≠a yo, y si me encontraba bien o no. Eras la persona con la que m√°s me gustaba pasar el rato hablando. Pr√°cticamente es lo que tenemos ahora, solo que antes no te sent√≠a en el pecho tanto como te siento hoy en d√≠a, ni me imaginaba un futuro junto a ti. Obviamente te quer√≠a much√≠simo pero hoy en d√≠a he pasado a amarte. Eres mi mejor amiga desde que te conoc√≠ y ahora tambi√©n pareja. Has tenido un impacto enorme sobre m√≠. Me encanta de verdad hacer llamada contigo. Escucharte a ti me parece mil veces mejor que escuchar cualquiera de mis canciones favoritas. Que me cuentes como te ha ido el d√≠a o c√≥mo te has peleado con alguna vieja en la playa me parece mil veces mejor que cualquiera de mis libros favoritos. Cuando me env√≠as fotos tuyas o videos de lo que est√°s haciendo me parece mil veces mejor que cualquiera de mis pel√≠culas favoritas. Te lo digo totalmente en serio. Podr√≠a estar viendo, leyendo o escuchando cualquier cosa que me encante de verdad, que si me llega un mensaje tuyo, todo lo dem√°s pasa a ser secundario. Puede que no sea sano tenerte tan arriba en mis prioridades. Por eso insisto que hagas tus cosas y que est√©s con tu gente antes que hablar conmigo. Obviamente quiero que me hables pero quiero que tu prioridad seas t√∫, no yo. Si estas viendo una serie con tu padre, quiero que la acabes con √©l y luego ya si eso la vemos nosotros. Si estas en el burguer con tus amigas, enviame 3 audios de las burradas que soltais y luego les haces caso a ellas. Tambi√©n me ayudar√° a m√≠ a acostumbrarme a no necesitar tu atenci√≥n 24/7. Y tambi√©n te ayudar√° a t√≠ que no est√© todo el d√≠a detr√°s. A veces antes de irme a mimir leo nuestras conversaciones, y hay d√≠as que volver√≠a al pasado y me dar√≠a un buen collej√≥n diciendome que te deje en paz ya. Si tienes paciencia conmigo, de verdad que mejorar√©. S√© que mejorar√© porque ya lo he hecho. No soy perfecto a√∫n y a lo mejor nunca lo ser√© pero ser√© mejor.")]),_c('p',[_vm._v("No te lo he dicho pero cuando me volviste a hablar, me sent√≠ horrible cuando me dijiste que no te hab√≠a felicitado para tu cumple. Yo de vdd pensaba que s√≠ que lo hab√≠a hecho. Lo siento de verdad. No s√© si se me olvid√≥ o me dio verg√ºenza hablarte por si ya no quer√≠as hablar conmigo (ya lo hemos hablado y se nota que no quer√≠as cortar la comunicaci√≥n conmigo pero mi cabez√≥n pensaba que s√≠). No s√© por qu√© no lo hice. No se me pudo haber olvidado. Pensaba en ti cada d√≠a. No lo entiendo. Perd√≥n de verdad. Siempre que pienso en lo que he hecho mal, me siento fatal. No lo digo pa darte pena. Quiero que sepas que lo siento de verdad. Mi memoria me impedir√° olvidarme de las putadas que te he hecho. La del cumplea√±os y la de \"me he ido a jugar con otra üíÖ\" son las que peor me saben pq las hice sin darme cuenta. Saber que te sent√≥ mal y yo pensando que era simplemente un comentario sin m√°s. No s√©, me da una sensaci√≥n rar√≠sima. La que menos me sabe mal es la que me fui del discord sin decir nada pq me mandaste a la mierda y creo que te desahogaste. Me acuerdo que me dijiste eso y dije \"pues si, hace bien mandandome a la mierda\". Irme ese d√≠a de discord era la peor decisi√≥n que pude haber tomado. Lo hice por una llamada de persona que me llevaba tiempo demostrando que no val√≠a la pena mantener cerca, y la puse por delante de ti. Culpa m√≠a por querer arreglar siempre las cosas. Tendr√≠a que haberte dado explicaciones claras en vez de decirte que me hab√≠a surgido algo urgente. Hoy en d√≠a no tomar√≠a esa decisi√≥n. El sticker la vdd que sospech√© de que fueras t√∫, te dije lo de \"tienes la cara tan triste como la de marta\" o algo as√≠ como comod√≠n por si me estabas poniendo a prueba pero weno, aun que sospechara no te reconoc√≠ as√≠ que no es excusa. Ahora que lo pienso es m√°s gracioso que \"malo\". No te hab√≠a visto mas que en 3 fotos de hangouts, y m√°s adelante ya vi que tenias las destacadas en Instagram y ah√≠ si que me grab√© tu cara en mis ojos. Me pas√© much√≠simo tiempo en tus destacadas. Recuerdo hasta las que borraste. Me has dicho que las borraste pq no te gustaban pero nose, sal√≠as muy guapa. Algo m√°s hay detr√°s seguro pero no te wa insistir a hablar de algo de lo que no quieres hablar. Luego est√°n los malentendidos. Lo de mateo, que hice mal dando cosas por hecho que no eran, y una vez que me enfad√© desproporcionalmente pq me ibas a decir algo y se te olvid√≥ pq estaban habl√°ndote en la playa, que tmbn hice mal dando por hecho que se te hab√≠a olvidado despu√©s de vacilarme, en vez de al principio. No olvido todo lo malo que he hecho, y a lo mejor est√° bien as√≠. Si no lo olvido, sabr√© actuar mejor la pr√≥xima, hasta el d√≠a que act√∫e bien en estas situaciones de forma natural. Estoy aprendiendo much√≠simo contigo. Ya no sobrepienso tanto, porque me transmites seguridad y confianza. Me est√°s ayudando a ser mejor persona, a superar muchas cosas que me llevaban carcomiendo desde hace tiempo, y a lo mejor ni te das cuenta de todo lo que he crecido como persona gracias a ti. Cada d√≠a desde que volvimos a hablar, ha sido mejor que el anterior. Saber que te acordabas de m√≠, y que yo te parec√≠a alguien que val√≠a la pena volver a hablarme, poco a poco me hac√≠a cada vez m√°s feliz. Eres todo lo que siempre he necesitado. Al principio ten√≠a alguna que otra inseguridad (bastantes), pero te juro que no era por no confiar en ti como persona, sino por cosas que ya he vivido. Y ese pasado e inseguridades me han hecho actuar de una manera que a m√≠ no me parece correcta en algunas situaciones, aunque no te haya sentado mal a ti. Confiaba en ti pero aunque me repitiera en la cabeza que no pasaba nada, me sent√≠a mal. A veces eran tonter√≠as como por ejemplo que subieras una historia normal en vez de para mejores amigos y me daba cosa que te viera otra gente. Otra veces era pq no actuaste como yo habr√≠a actuado y no hay nada de malo en eso, no somos la misma persona y no actuaremos igual en la misma situaci√≥n. Con mateo por ejemplo yo le habr√≠a dicho de primeras que me ha incomodado lo que ha hecho y prefiero no seguir hablando con √©l, pero yo soy bastante mas cortante en estas cosas, y me centro m√°s en c√≥mo me siento yo que en c√≥mo se sentir√° la persona que me esta molestando. Entiendo que no seas tan \"fr√≠a\" y tampoco quieras quedar mal dici√©ndole que no te hable nunca mas y todo eso. Me molestaba en su d√≠a pq yo lo ve√≠a como que estabas poniendo los sentimientos de mateo antes que los m√≠os. Pero yo no estaba pensando en los tuyos, en c√≥mo te afectar√≠a a ti decirle algo as√≠, y c√≥mo quedar√≠as con tu c√≠rculo de gente. No tuve paciencia para ver c√≥mo lo resolv√≠as t√∫, quer√≠a resolverlo cuanto antes y te presionaba demasiado. No era desconfianza. Estaba convencido de que pod√≠a confiar en ti, y a√∫n as√≠ ten√≠a miedo que fueras como otra gente que me hizo da√±o de una manera muy parecida, o miedo a que me abandonaras. Por eso tmbn me pegaba m√°s de cerca. Era est√∫pido porque t√∫ diste el primer paso, y s√≥lo con eso ya deber√≠a dejar de lado la mayor√≠a de mi miedo al abandono. No me has dado ni una sola raz√≥n para desconfiar de ti y odio cuando me pongo as√≠. Eso no es lo que yo quiero ser, ni lo que quiero darte como persona. Mereces m√°s que eso. Me pareces alguien incre√≠ble y sigo sin entender c√≥mo puedes ser m√≠a. Ya casi nunca me siento inseguro respecto a ti. Me has demostrado que eres buena persona, que merece la pena mejorar por ti, y que no tiene sentido sentirse inseguro contigo. De verdad que me has tratado muy bien. Has sacado lo mejor de m√≠, y has hecho desaparecer muchas cosas que yo consideraba malas sobre m√≠. Aunque a lo mejor haya d√≠as donde me cueste expresarlo m√°s, aunque haya d√≠as que nos enfademos, aunque haya d√≠as que no hablemos tanto, nunca dudes de mi amor por ti. Primero est√°s t√∫, y luego est√°n las discusiones y los malos d√≠as. Por tu parte no parece que tengas ninguna inseguridad en lo que respecta a m√≠. O las disimulas bien. Por si acaso te lo voy a decir de todas formas. Yo s√© que las palabras son d√©biles contra los pensamientos malos y probablemente esto no ayude en nada en caso de que tengas miedo de que me vaya o cualquier cosa, pero quiero que sepas que eres mi todo. No te quiero para pasar el rato cuando me aburro o cuando no tengo nada que hacer. Te quiero siempre da igual d√≥nde est√© y da igual qu√© est√© haciendo. Cuando juego a algo me gustar√≠a que estuvieras en la habitaci√≥n conmigo. Cuando veo pel√≠culas o series me gustar√≠a que estuvieras viendolas conmigo. Cuando voy a hacer el kfe, o me voy de viaje, quiero que est√©s conmigo disfrutandolo. Cualquier cosa que hago ahora, parece que no est√° completa si t√∫ no formas parte de ella. Disfruto much√≠simo de tu compa√±√≠a ya sea en discord o simplemente hablando por chat. La disfruto m√°s que la de cualquier otra persona que est√© f√≠sicamente conmigo. No puedo ni imaginarme lo feliz que ser√© cuando te tenga delante de verdad. S√© que sabes que te quiero pero te quiero m√°s de lo que sabes. Te quiero a t√≠ y s√≥lo a t√≠. Y lo digo literalmente. Podria tener un clon tuyo delante, con los mismos recuerdos y personalidad que t√∫, pero no ser√≠as t√∫. No ser√≠a la adriana que me ha aguantado tantos a√±os. No ser√≠a la adriana que me ha enamorado. No ser√≠as t√∫. Yo te quiero a t√≠. No a alguien como t√∫. A t√≠. Y si no quiero un clon exacto tuyo, ya puedes imaginarte lo poco que me interesa otra persona. En mi vida solo hay sitio para una persona y esa eres t√∫. Y pase lo que pase est√° reservado para t√≠. Si llega el d√≠a que la vida nos separe por cualquier motivo, ese sitio en mi vida se cerrar√°. Nadie mas va a poder entrar, y tampoco quiero que entre nadie m√°s. Me has marcado para siempre de verdad. No quiero tu personalidad ni tu aspecto ni tu forma de tratarme si no eres t√∫ la persona que me da esas cosas. Hay gente que a lo mejor podr√≠a acercarse a como eres pero no ser√≠an t√∫. Te quiero a t√≠. Por si no lo he dicho a√∫n, te quiero a t√≠ y a nadie m√°s. Siempre te querr√© a t√≠. Solo tu, tu y tu. De verdad que tu amor es el √∫nico qu√© necesito. Es bonito sentirme amado por t√≠ sin siquiera poder abrazarnos. No cambiar√≠a tu amor por nada en el mundo.")]),_c('p',[_vm._v("Cuando hago algo mal, y te digo que es pq tengo malos h√°bitos por cosas del pasado, no lo digo para que me perdones por pena ni para justificarme ni nada. Lo digo para que entiendas que lo que hago mal, no lo hago porque me hayas generado desconfianza t√∫, sino que viene de antes. Tener problemas de antes, no justifica algunas cosas que te hago. No me estoy excusando cuando menciono mi pasado. Quiero que entiendas pq hago las cosas que hago y que no son tu culpa. Te prometo que van a desaparecer mis malos h√°bitos. C√≥mo no van a desaparecer. Me tratas incre√≠ble. No te culpo a ti ni me excuso cuando hago algo mal. Acepto mis errores y me culpo a m√≠ por ellos. No quiero pagar contigo cosas que no son tu culpa. No es tu responsabilidad cerrar mis heridas y tampoco quiero que lo sea. Si quieres ayudar, simplemente siendo t√∫ misma ya me ayudas. Me est√°s ayudando simplemente existiendo. Te prometo que voy a mejorar. No soy perfecto, pero ya he mejorado en muchas cosas, y s√© que contigo voy a mejorarlas todas.")]),_c('p',[_vm._v("Cada d√≠a que pasa te necesito m√°s. Nada mas despertar, tengo una necesidad incre√≠ble de tenerte a mi lado y abrazarte muy fuerte, de dejarte sin aliento con tremendo apret√≥n. No puedo ni imaginarme el d√≠a que nos veamos. No s√© qu√© hacer. Te abrazo? Te doy la mano? No puedo parar de pensar en ello. Que hacer para pasar el d√≠a? Donde te llevo? Te aburrir√°s conmigo? Me quedar√© en blanco por los nervios? De qu√© hablaremos? S√© que me pondr√© muy nervioso. Tengo unas ganas incre√≠bles de que llegue ese d√≠a, y a la vez es de lo que m√°s miedo me da en el mundo. Por mucho que hablemos por texto o llamada, me podr√°n los nervios. Me da miedo decepcionarte o aburrirte en persona. En cambio me duele much√≠simo no ser parte de los d√≠as en que te lo pasas bien cuando vas a la playa o sales por el centro y te lo pasas bien. Es decir, me alegra much√≠simo que tengas buenas amigas como marta y que salgas tanto con tu familia por ah√≠, me refiero a que me duele no ser parte de eso.")]),_c('p',[_vm._v("Llevo d√≠as sin escribir nada, no sab√≠a qu√© m√°s decir. Voy a intentar explicar un poco lo que siento por ti en un p√°rrafo dedicado expresamente para ello en vez de explicarlo de manera desperdigada. S√© que me quedar√© corto porque no s√© c√≥mo hacerte entender el 90% de lo que me haces sentir. S√© que ya sabes que te amo y que quiero vivir mi vida junto a ti, pero hay mucho m√°s que eso. Ans√≠o tu compa√±√≠a como nada en este mundo. Te juro que eres el centro de mi vida. Da igual lo que est√© haciendo, si me llega un mensaje tuyo, lo que estaba haciendo antes pasa a segundo plano. Eres mi primer√≠sima prioridad. Si estoy ensayando, y veo que me llega un mensaje a mitad canci√≥n, deseo que acabe lo antes posible para ver qu√© has escrito. Si estoy jugando a algo, intento acabar la partida lo antes posible para hablarte sin distracciones. Me gusta much√≠simo de verdad hablar contigo. Ojal√° nunca te calles nada pensando que eres pesada. Me dar√≠a rabia perderme una conversaci√≥n contigo pq pienses que no quiero hablarte o algo as√≠. De verdad que nunca tengo suficiente de ti. Podr√≠a estar 24/7 habl√°ndote. Tendr√≠as que ver lo feliz que me pongo cuando me hablas. Se me cierra el est√≥mago y noto literalmente cosquillas en el coraz√≥n, y que yo sepa no tengo problemas de circulaci√≥n. Se me suben las pulsaciones y me pongo ansioso en el buen sentido. Noto una euforia incre√≠ble. Me haces espermar cosa mala. Me encanta hablar hasta las 4 de la ma√±ana contigo. Espermando alt√≠simo. Te he pillado una confianza incre√≠ble. Nadie que me conozca se creer√≠a lo que he hecho contigo. Contarte los sue√±os, enviarte los audios, literalmente decirte que toy trabajando en mis manualidades mientras tamos hablando. Ni siquiera yo me lo creo del todo. Si le dijera esto a mi yo del pasado, pensar√≠a que me estoy vacilando. Estoy feliz de poder decirte estas cosas sin verg√ºenza alguna. Me daba miedo decir algunas cosas por si acaso, y a la vez me daba miedo nunca pillarte la confianza para decirte nada de esto, pero ya no te tengo filtro. Espero de vdd que no te moleste ni incomode, y que no tengas miedo de decirme cuando algo no te gusta. Obviamente la mayor√≠a de d√≠as te necesito cerca, pero no por espermacion, sino pq de vdd que me entran unas ganas de abrazarte, tenerte simplemente al lado aunque no estemos hablando, tener tu presencia. Poder decirte \"Quieres ir por ah√≠?\" y que puedas decir que s√≠ y nos vamos por ah√≠ de verdad. Quiero que cuando acabemos de jugar o ver una peli, en vez de despedirnos e irnos a Instagram, podamos seguir hablando en persona e irnos a mimir juntos. No tienes ni idea de cuanto deseo eso. M√°s que nada en este planeta. No s√© c√≥mo explicar con m√°s detalle el efecto que tienes en m√≠. No eres consciente de lo que me ayudas. Se me contagia tu alegr√≠a y tu tristeza, celebro tus logros como si fueran m√≠os y sufro tus preocupaciones como si fueran m√≠as. Mi estado de √°nimo depende much√≠simo de ti y me gusta que sea as√≠. Quiero pensar que significa que no estoy siendo ego√≠sta. No s√≥lo me interesa tu compa√±√≠a para sentirme bien yo, sino que te tengo empat√≠a y me interesa c√≥mo te sientes t√∫. Quererte en mi opini√≥n no es solo querer que est√©s a mi lado para que me hagas sentir bien a m√≠ y ya. No solo quiero tu compa√±√≠a, quiero que triunfes en tu vida. No hay nada que me gustar√≠a m√°s que verte cumplir tus objetivos, y que seas feliz. Ver como te vuelves inspectora de la UDYCO, que encuentres un equipo de v√≥ley que te guste e incluso te vuelvas jugadora profesional. Ver una sonrisa en tu cara cada d√≠a por haber logrado lo que deseas. Ver c√≥mo se acaban los dolores de cabeza, la ansiedad y el malestar que llevas aguantando tanto tiempo. Todo eso es lo que me har√≠a feliz. Ya te lo he dicho, se me contagia tu estado de √°nimo. De verdad que estoy muy orgulloso de ti. Haber aguantado tant√≠sima mierda tanto tiempo, c