do son qu√≠micos en el cerebro y me da miedo que el tuyo decida que ya no merezco la pena, o que otro merece m√°s la pena. Me veo a m√≠ mismo bastante mal la verdad. Y esta inseguridad me hace temblar cuando hay un m√≠nimo de probabilidades de que pierdas inter√©s. Interpreto cualquier cosa de la peor forma posible. Si no me hablas en 2 horas pq est√°s en la feria con marta ya me siento autom√°ticamente como si no fuera una prioridad para ti. S√© que no es as√≠ pero f√≠sicamente me duele como si lo fuera. Es otra vez la mierda esta de ser consciente de que no pasa nada, pero estar retorci√©ndome f√≠sicamente. Es un poco desastre este p√°rrafo. Hablo de todo un poco pq me iba dando cuenta de cosas a medida que escrib√≠a, y he vuelto d√≠as despu√©s, tras haberlo pensado y haber entendido pq act√∫o de cierta manera. Me he dado cuenta que la base de todo lo que hago mal son mis inseguridades, y t√∫ me est√°s ayudando a superar much√≠simas. As√≠ que no miento cuando digo que eres mi medicina, que me est√°s curando de verdad.")]),_c('p',[_vm._v("De verdad que le he dado mil vueltas a todo lo que te ha hecho sentir mal en alg√∫n momento. Sobre todo mi curiosidad. Cuando decides que prefieres no decirme algo, cuando est√°s enfadada y dices \"nada\" o lo que sea. S√© que a veces simplemente deber√≠a dejar de preguntar y olvidarlo pero me da miedo que lo interpretes como que estoy perdiendo el inter√©s o que ya no me importa tanto lo que sientes. Todo lo que pienso que puedo hacer, me da la sensaci√≥n que es malo. Si insisto, ser√© un pesado que te presiona para que me lo digas. Si no, ser√© un pasota que le da igual lo que sientas. Esque es absurdo. No entiendo por qu√© no encuentro el punto intermedio como cualquier otra persona normal. A veces de verdad pienso que tengo autismo o algo jsjd. Esque cosas as√≠ nunca me han preocupado. Nunca me ha importado que alguien me diga \"da igual olv√≠dalo\" hasta que llegaste t√∫. Es dif√≠cil controlar algo que empec√© a experimentar hace poco, y tan tarde en mi vida. Me preocupas much√≠simo y no s√© echarle el freno a tiempo. Ya sea por curiosidad o preocupaci√≥n de que est√°s mal. Siento una necesidad de saberlo todo, de que no se me escape ni un detalle. No s√© por qu√©. Cuando te callas alguna ovulada, me da mucha curiosidad y obviamente me gusta que sueltes comentarios as√≠ sobre m√≠. Cuando est√°s mal y prefieres no hablarlo s√≠ que te digo que lo hablamos otro rato pero me sigo estremeciendo pensando en si es algo muy serio o algo. Tengo que aprender a no saberlo todo. Y sobre todo aprender que no contarme algo no significa que no conf√≠as en m√≠.")]),_c('p',[_vm._v("Me cuesta much√≠simo centrarme en otras cosas. Estoy todo el rato pensando en qu√© estar√°s haciendo, en si tmbn estar√°s pensando en m√≠, en si te lo estar√°s pasando bien. Pensando en escribirte, en empezar una conversaci√≥n contigo. Cuando sales de casa o te echas siesta, s√≥lo espero a que vuelvas. Ni as√≠ te me quito de mi cabeza. De verdad que si yo soy polilla t√∫ eres l√°mpara. Eres mi zona segura. Puedo contarte absolutamente todo. Lo que sea. Puedo decirte cualquier cosa sin preocuparme de que acabes us√°ndola en mi contra. No criticas (al menos en serio) todo lo que hago. Me haces sentir que no soy tan mierdas como pensaba. Simplemente eres tan buena para m√≠ que no soy capaz de dejar de pensar en ti ni por 1 segundo. Me traes loquito pero literalmente.")]),_c('p',[_vm._v("No est√° siendo demasiado dif√≠cil la distancia para m√≠. Es verdad que muchas veces me haces much√≠sima falta, y me dan unas ganas tremendas de poder abrazarte, pero no me siento vac√≠o. Me llenas much√≠simo aun estando tan lejos. La distancia simplemente es la prueba de que nos queremos por mucho m√°s que el contacto f√≠sico. Estoy muy contento contigo. Obviamente desear√≠a que estuvi√©ramos juntos f√≠sicamente pero no sufro demasiado. Nuestras llamadas y charlas por wasap me llenan de verdad. Cuando te digo que eres much√≠simo m√°s que suficiente, en parte me refiero a esto. No tengo que ver tus malcriadas ni hacer el pitochocho para ser feliz contigo. De verdad que tienes much√≠simo m√°s que ofrecer, y se nota.")]),_c('p',[_vm._v("Cuando acab√© la eso no estaba bien. Llevaba toda mi vida de broncas constantes por las notas. Era un faltao, nunca hac√≠a los deberes en primaria y estaba mi agenda llena de broncas de profesores que mis padres ten√≠an que firmar. La eso la aprob√© just√≠simo. Todo el mundo diciendo que si segu√≠a as√≠ acabar√© mal, que si no me pongo las pilas no ser√© feliz. Cuando empec√© inform√°tica, me iba bien. Aprobaba con buenas notas sin esforzarme nada y conoc√≠ a buenos amigos como Miquel Alex y To√±. Pero no sab√≠a pq, no me sent√≠a bien. Aunque sacara matriculas de honor o lo que sea no taba contento. Sin m√°s. Otro aprobado y ma√±ana ser√° otro d√≠a. Mas bien iba todo para abajo. En el ciclo superior me separ√© de los que conoc√≠a y todos tenian una edad que no les interesaba conocer a gente. Estuve 2 a√±os simplemente existiendo. T√∫ eras mi escapatoria. Lo √∫nico que ten√≠a. Y me ayudabas muchisimo. Pero todo lo bueno que me dabas no me quitaba los malos penamientos. No era feliz con mi pareja, ni con mi vida en general. Conoc√≠ a gente en otros paises y hice un par de viajes bastante lejos de casa pero cuando volv√≠a me volvia a sentir mal. Ten√≠a el pensamiento de \"weno wa seguir palante y si sale muy mal todo pues siempre queda la salida f√°cil\". No lo ve√≠a preocupante pq pensaba \"todo el mundo lo habr√° pensado alguna vez\". Luego el pensamiento se convirti√≥ en un \"mam√° estar√° triste, se culpar√° por esto\", y acab√≥ en algo mas ego√≠sta como \"si no estoy aqu√≠ pa presenciarlo, no me sentir√© mal por que llore mama\". Hace un a√±o que ya lo ten√≠a decidido. Gastaba much√≠simo dinero pq pensaba \"para qu√© ahorrar\". Me mataba a comida de mierda pq pensaba \"no morire de un infarto\". Me mat√© a caprichos antes de matarme de verdad. Dej√© de preocuparme por mantener amistades, y por ser buena gente en general. Lleg√≥ mi cumplea√±os y ya ten√≠a decidido que no llegar√≠a a nuevo a√±o. Tuve el sue√±o que te cont√© de la galaxia explotando. Quer√≠a despedirme antes de hacer nada. De repente me empezaste a seguir por instagram. Ya te sabes el resto.")]),_c('p',[_vm._v("Hoy me has dicho que tienes una vocecita en la cabeza como la que tengo yo. Te dice que no te tomo enserio o que no est√°s a la altura de otra gente. Es raro decir esto pero me ha dado seguridad que pienses eso. Que tengas esos miedos al abandono igual que yo. Yo s√© cu√°nto te amo. Yo s√© que no tienes motivos para temer que te abandone. Y aun as√≠ tienes miedo. Eso me ayuda a creerme que yo tambi√©n tengo miedo sin motivos. Que sientes much√≠simas cosas por m√≠. Cosas que no me cre√≠a que sent√≠as nunca por m√≠. Y que estoy en la misma situaci√≥n que t√∫. Que me amas much√≠simo y que no ves nada malo en m√≠ de verdad. Que no me mientes para proteger mis sentimientos. Y que no tiene sentido tener miedo. Que tengas miedo que me vaya tambi√©n me hace pensar que te importa much√≠simo lo nuestro. M√°s de lo que pensaba. No s√© si es ego√≠sta sacar algo bueno para m√≠ en base a algo malo para t√≠. Es lo de siempre. Duele tener miedo al abandono pero es bonito tener a alguien que temes perder. Espero que al leer la cara y ver todo lo que me ha preocupado siempre, todo lo que he superado por t√≠, y todo lo mal que lo pas√© sin t√≠, te quite esa vocecita. Ojala veas lo mucho que me interesa lo nuestro, y te des cuenta de que no tienes que tener miedo de que me vaya pq contigo acabo hasta el final. No hay otro plan. Ojal√° te pudiera decir esto ahora pero me da miedo que si no ves el resto de la carta, esto sean solo palabras vac√≠as. No me voy a ir a ning√∫n lado.")]),_c('p',[_vm._v("Te has sentido mal porque he dicho \"no estoy perdiendo sentimientos por t√≠\". Lo entiendo. Es de mongolo soltar algo as√≠ para la tonter√≠a de la que estaba hablando. Pero me ha dado a pensar. Todo esto que siento (e intuyo que tu tambi√©n), puede que deje de sentirlo alg√∫n d√≠a. O puede que no. Qui√©n sabe. Lo que s√© es que son hormonas o qu√≠micos en el cerebro que podr√≠an dejar de producirse en cualquier momento. Lo que tambi√©n s√© es que aunque deje de sentir mariposas y todo esto, seguir√© amandote. Seguir√© eligiendote a t√≠. Quiero hacer mi vida contigo. No te he elegido a t√≠ √∫nicamente por las cosquillas en el coraz√≥n. Me pareces una muy buena persona. Me atraes f√≠sicamente. Me tratas bien. Te puedo asegurar que si dejan de producirse esas hormonas y qu√≠micos en mi cerebro, te seguir√© escogiendo a t√≠. Conozco gente que me ha dicho que se sent√≠a igual que yo y dej√≥ de sentirlo despu√©s de un tiempo, pero siguen siendo felices. En cambio mi padre dice que sigue sintiendo las mariposas y el cosquilleo en el coraz√≥n as√≠ que a lo mejor he pillado su gen√©tica y me dura toda la vida. La verdad me cuesta creer que perder√© esta sensaci√≥n alg√∫n d√≠a. Lo siento todo tan intensamente que no veo posible que deje de sentirlo nunca, al menos no del todo. Con esto quer√≠a decir que aunque mi cuerpo se acostumbre a t√≠, y deje de hacerme sentir as√≠, no quitar√° el hecho de que quiero una vida junto a t√≠. Me gusta lo que siento hoy en d√≠a, pero estoy contigo pq me gustas t√∫, no las cosquillas. Te quiero a t√≠ pq me lo paso bien contigo, me atraes f√≠sicamente, me tratas bien, se pueden hablar las cosas contigo, y me has ayudado como nadie lo ha hecho nunca. Con o sin cosquillas, me quedar√© a tu lado. Las cosquillas son algo que han hecho que empiece todo esto, pero que se marchen no significa que me vaya a marchar yo. De momento te aseguro que me sigues poniendo nerviosisimo en el buen sentido. Ya te he dicho varias veces que me hablas y se me cierra el estomago. Sigo sintiendo todo con muchisima intensidad, y no hay se√±ales de que vaya a cambiar en mucho tiempo. Quiero que sepas que mis hormonas y quimicos no los controlo ni yo ni t√∫. Si se pierde, no es por algo que hayas hecho t√∫ ni algo que haya hecho yo. Y quiero que sepas que segurias siendo mi elecci√≥n y mi prioridad. Cuando te digo que te amo no solo me refiero a esos sentimientos. Me refiero a que me preocupo por t√≠, me intereso por t√≠, y me atraes. No s√© si recibir√°s este p√°rrafo con la misma intenci√≥n con la que lo escribo. Quiero que entiendas que por como me siento hoy en d√≠a, me atrever√≠a a jurar que nunca dejar√© de sentir esto por t√≠. De verdad que es muy intenso lo que me haces sentir. Pero lo quer√≠a mencionar. Quiero darte seguridad, que para estar conmigo no dependes de los qu√≠micos de mi cerebro. Quiero que sepas que aunque no sienta las cosquillas, me seguir√° pareciendo muy bonito lo nuestro. Seguir√© disfrutandolo. Seguir√© mejorando por esto. Seguir√© ilusionado contigo. Las cosquillas son un 1% del motivo por el que estoy contigo. Son un 1% de lo que siento por t√≠. Pq aparte de las cosquillas, est√° la tranquilidad que me transmites, la sensaci√≥n de que eres mi zona segura, lo bien que sienta c√≥mo me tratas, lo bien que sienta cuando te hago re√≠r. No s√© si este p√°rrafo ha sido buena idea. A lo mejor lo borro. Si no lo acabo borrando y te ha puesto triste algo, lo hablamos porfi. Ya s√© qu√© puedes llegar a pensar pq a m√≠ me pas√≥ tambi√©n cuando dijiste algo tipo \"en la uni a lo mejor se enfrian las cosas\". Cuando dijiste eso, pens√© \"joer yo seguir√© hablandote 24/7 y to eso\" aunque sab√≠a perfectamente que te refer√≠as a que simplemente no tendras tanto tiempo. Con este p√°rrafo puede que ahora pienses \"si est√° diciendo que puede que pierda sentimientos en un futuro, es pq ahora no lo siente lo suficiente como para pensar que durar√° para siempre\" o algo por el estilo. Te aseguro que no es as√≠. Lo que siento ahora te juro que no tiene sentido. Y aunque deje de sentirlo alg√∫n d√≠a, s√≥lo son las cosquillas. Ya te he dicho que para m√≠, amarte es mucho m√°s que eso. Si se van las cosquillas, pos que se vayan. Quedar√° mi empat√≠a, preocupaci√≥n, inter√©s, atracci√≥n, y otras mil sensaciones que me haces sentir cuando r√≠es o lloras o solucionamos una discusi√≥n o lo que sea. S√© que estos sentimientos nunca se ir√°n. Esto te lo juro. Y lo puedo jurar pq esto s√≠ que se puede controlar. Siempre sienta bien arreglar las cosas o re√≠r con alguien. Siempre sienta bien desahogarse con alguien. Y contigo sienta 184882842882 veces mejor. Y siempre ser√° as√≠. Si no borro este parrafo, por favor te lo suplico. Si algo te ha sentado raro, me lo dices y lo hablamos. Amor, no necesito las cosquillas para querer estar contigo. Ya me hac√≠as muy feliz mucho antes de sentirlas, y seguir√°s haciendolo aunque deje de sentirlas, con el a√±adido de que ahora s√© que nos amamos. Y otra vez, esto no es un \"acabar√© perdiendo esa sensaci√≥n de cosquilleo\", es un \"si pasa, no cambiar√° nada\". Puedes estar segura de ello. Te juro que si pierdo la sensaci√≥n esa, no te lo ocultar√©. Tampoco te dir√© \"e que se han ido las cosquillas\" pero si me preguntas no tendr√© ning√∫n problema en decirte la verdad pq solo son una √≠nfima parte de lo que me gusta de t√≠. Lo juro.")]),_c('p',[_vm._v("S√© lo que quiero. Llevo vivo bastante tiempo, y me gusta pensar que me conozco bien, y entiendo lo que quiero. Y te quiero a ti. No estoy \"probando\" a ver si aguanto hasta vernos en persona. Si a√∫n no he parado esto contigo, es porque s√© que esperar√© lo que haga falta si eso significa estar contigo. S√© que vales la pena. S√© que me gustas. S√© que te amo. S√© que soy feliz contigo. No pienso \"ser√© feliz con ella en un futuro\". Ya lo soy en el presente. S√© que por ti, superar√© lo que sea. S√© que contigo ser√° much√≠simo m√°s f√°cil convertirme en alguien que me guste ser. S√© que no estoy tanteando. Estoy decidido. No hay plan B pq no lo necesito. Pq s√© que lo que quiero eres t√∫. Todos los sue√±os que ten√≠a de ni√±o, todos los planes que hice de adolescente, y los pensamientos que ten√≠a de adulto, todo eso ha cambiado. Todos mis sue√±os, planes y pensamientos, te incluyen a ti. No hay sitio en mi mente para pensamientos como \"si me deja pos har√© esto o lo otro\". Solo caben pensamientos de \"me gustar√≠a hacer esto con ella\". Amor, estoy convencid√≠simo que eres t√∫. Al 100%. No tengo ni una sola duda.")]),_c('p',[_vm._v("Creo que he sido bastante ego√≠sta este tiempo. No en el sentido de que no me importas o algo as√≠, sino en el sentido de que te he utilizado para calmar mis miedos. Y en principio no est√° mal, somos pareja, nos utilizamos el uno al otro para calmar nuestras preocupaciones, y m√°s si tienen que ver con nosotros. Pero me he pasado. Much√≠simo. He sido (y aunque ya no tanto, creo que sigo siendo) muy inseguro. Y a la m√≠nima te ped√≠a explicaciones o iba llorando para que calmes mi vocecita. Y no estar√≠a mal si esa vocecita tuviera motivos serios reales, pero he visto un espacio seguro contigo, y lo he exprimido al m√°ximo, sin tener cuidado de qu√© cosas estoy metiendo en ese espacio. Lo he llenado de tonter√≠as, haciendo que no quede espacio para cosas serias. Te he fatigado con discusiones est√∫pidas. Te he exigido demasiado. Me peleo con la vocecita. A veces gano y a veces pierdo. La cosa es que gane o pierda, muchas cosas son tonter√≠as, y las cosas serias que me dan miedo, ya me has dicho que no son as√≠. Sobrepienso absolutamente todo. Cada mensaje o cambio de tono, los d√≠as que hablamos menos, los d√≠as que te noto menos cari√±osa. Y esque probablemente el 90% de esos d√≠as, ni siquiera est√©s diferente y simplemente soy yo que estoy raro y veo cosas donde no las hay. Estoy intentando dejar de escribirte parrafadas como lo hac√≠a, pq creo que s√≥lo te confunden, no me explico bien, te abruman, y algunas hacen que parezca mucho m√°s serio de lo que es. Estoy intentando aguantar la ansiedad y el dolor en el pecho de cuando sobrepienso a ver si con el tiempo se va. Intento distraerme. Intento entender que por